import React, { useState } from "react";
import { Header } from "./Header/Header";
import { Players } from "./Header/Players";
import { ScoreBoard } from "./ScoreBoard";
import "./styles.css";

export default function App() {
  const [player1health, setPlayer1Health] = useState(100);
  const [player2health, setPlayer2Health] = useState(100);

  let [player1score, setPlayer1Score] = useState(0);
  let [player2score, setPlayer2Score] = useState(0);
  let [roundcounter, setRoundCounter] = useState(0);

  function shootBtn() {
    let player1fire = Math.floor(Math.random() * 5);
    let player2fire = Math.floor(Math.random() * 5);

    setPlayer1Health(player1health - player1fire);
    setPlayer2Health(player2health - player2fire);

    document.getElementById("p1").innerHTML =
      "player 1 is firing with a " +
      player1fire +
      "power And reamining health is: " +
      player1health;
    document.getElementById("p2").innerHTML =
      "player 2 is firing with a " +
      player2fire +
      "power And reamining health is: " +
      player2health;

    console.log("player1's remaining health is:" + player1health);
    console.log("player2's remaining health is:" + player2health);

    setRoundCounter((roundcounter = roundcounter + 1));
    console.log(roundcounter);
    document.getElementById("game").innerHTML = "Game" + roundcounter;

    if (player1health === 0) gameover("Player 2 Won");

    if (player2health === 0) gameover("Player 1 Won");

    if (roundcounter === 5) {
      document.getElementById("shootBtn").disabled = true;
      document.getElementById("shootBtn").innerHTML = "Game over";
      document.getElementById("shootBtn").style.backgroundColor = "grey";
    }

    if (player1fire > player2fire) {
      player1score++;
      document.getElementById("item-3").innerHTML = player1score;
    } else if (player1fire < player2fire) {
      player2score++;
      document.getElementById("item-6").innerHTML = player2score;
    }

    console.log("player1 score is:" + player1score);
    console.log("player2 score is:" + player2score);

    setPlayer1Score(player1score);
    setPlayer2Score(player2score);

    if (player1score === 3) gameover("Player 1 won");

    if (player2score === 3) gameover("Player 2 won");

    if (roundcounter === 5) {
      if (player1score > player2score) gameover("Player 1 won");
      else if (player2score > player1score) gameover("Player 2 won");
      else gameover("Match draw");
    }
    console.log(" ");
  }

  function gameover(comment) {
    document.getElementById("item-9").innerHTML = comment;
    document.getElementById("shootBtn").disabled = true;
    document.getElementById("shootBtn").innerHTML = "Game Over";
    document.getElementById("shootBtn").style.backgroundColor = "grey";
  }

  function restart() {
    location.reload();
  }

  return (
    <div className="App">
      <Header />
      <Players />

      <br />

      <button id="shootBtn" onClick={shootBtn}>
        Shoot
      </button>
      <span>
        <button id="restart" onClick={restart}>
          Restart
        </button>
      </span>

      <br />
      <br />

      <ScoreBoard />

      <div className="result">
        <div className="result-item" id="item-9"></div>
      </div>
    </div>
  );
}
